@include tablet {
  .contenedor-anuncios {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 2rem;
    margin-top: 0;
  }
}

.anuncio {
  background-color: lighten($gris, 5);
  border: 1px solid darken($gris, 10);
  margin-top: 2rem;

  @include tablet {
    margin-top: 0;
  }

  .contenido-anuncio {
    padding: 2rem;
  }

  h3 {
    font-weight: bold;
  }

  h3,
  p {
    margin: 0;
    text-align: center;
  }
}

.precio {
  color: $verde;
  font-weight: bold;
  font-size: 3rem;
  text-align: center;
  padding-top: 1rem;
}

.iconos-caracteristicas {
  display: flex;
  padding: 0;
  justify-content: space-evenly;
}

li {
  display: flex;

  img {
    margin-right: 2rem;
    min-width: 2.5rem;
  }

  p {
    font-size: 2.5rem;
    padding-right: 1.5rem;
  }

  &:last-child {
    p {
      padding-right: 0rem;
    }
  }
}
