.modo-noche {
  background-color: darken($grisOscuro, 10);
  p {
    color: $blanco;
  }
  h1,
  h2,
  h3,
  h4 {
    color: $gris;
  }
  .anuncio {
    background-color: $grisOscuro;
    border: 1px solid $grisOscuro;

    .precio {
      color: $verde;
    }
  }
  blockquote {
    color: $gris;
  }
  li {
    img {
      filter: invert(100%);
    }
  }
  .formulario {
    label,
    legend,
    p {
      color: $gris;
    }
    textarea,
    select,
    input:not([type="submit"]) {
      background-color: $grisOscuro;
      color: $blanco;
      border: 1px solid $grisOscuro;
    }
  }
  table.propiedades {
    color: $blanco;
    tbody {
      tr {
        border-bottom: 0.5rem solid $grisOscuro;
      }
    }
  }
}
