.iconos-nosotros {
  @include tablet {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 2rem;
    gap: 2rem;
  }
}
.icono {
  padding-top: 5rem;
  text-align: center;
  justify-content: center;
  @include tablet {
    padding-top: 0rem;
  }
  img {
    height: 10rem;
  }
  h3 {
    text-transform: uppercase;
  }
}
