.barra {
  display: flex;
  flex-direction: column;
  padding-top: 3rem;
  align-items: center;
  justify-content: space-between;

  @include tablet {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navegacion {
  height: 0;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.5s ease-out, opacity 0.5s linear, height 0.5s linear;
  @include tablet {
    display: flex;
    align-items: center;
    height: 100%;
    visibility: visible;
    opacity: 1;
  }
  a {
    font-size: 1.8rem;
    color: $blanco;
    text-decoration: none;
    display: block;
    text-align: center;
    &:hover {
      color: $amarillo;
    }

    @include tablet {
      margin-right: 2rem;
      &:last-of-type {
        margin-right: 0;
      }
    }
  }
}

.contenedor-navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  @include tablet {
    align-items: flex-end;
  }

  .dark-mode {
    margin: 2rem 0;
    width: 3rem;
    filter: invert(100%);
    &:hover {
      cursor: pointer;
    }
    @include tablet {
      margin-top: 0;
    }
  }
}

//Menu de hamburguesa//

.mobile-menu {
  margin-top: 2rem;
  @include tablet {
    display: none;
  }
  img {
    width: 5rem;
  }
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: 100%;
  transition: visibility 0.5s ease-out, opacity 0.5s linear, height 0.5s linear;
}
