.webp .contacto-banner {
  background-image: url(../img/encuentra.webp);
}
.no-webp .contacto-banner {
  background-image: url(../img/encuentra.jpg);
}

.contacto-banner {
  background-size: cover;
  background-position: center center;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 8rem 0rem;
  h2 {
    font-weight: bold;
  }
  h2,
  p {
    color: $blanco;
  }
}
