.alerta {
  margin: 1rem 0;
  text-align: center;
  color: $blanco;
  text-transform: uppercase;
  font-weight: bold;
  &.error {
    background-color: red;
  }
  &.exito {
    background-color: $verde;
  }
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0rem;
  border-collapse: collapse;

  th {
    color: $blanco;
    padding: 2rem;
  }
  .imagen-preview {
    display: block;
    margin: 0 auto;
    width: 20rem;
  }

  tbody {
    tr {
      border-bottom: 0.5rem solid $gris;
      &:last-child {
        border-bottom: none;
      }
      td {
        text-align: center;
        padding: 2rem 0rem;
      }
    }
  }
}

.imagen-tabla {
  width: 30rem;
}

.cabecera-verde {
  background-color: $verde;
}
.cabecera-amarilla {
  background-color: $amarillo;
}
.cabecera-gris {
  background-color: $grisOscuro;
}
