.boton-amarillo-block {
  @include boton($amarillo, block);
}
.boton-amarillo-no-block {
  @include boton($amarillo, inline-block);
}

.boton-verde {
  @include boton($verde, inline-block);
}
.boton-contacta {
  @include boton($amarillo, block, 2rem, 2rem, 2.5rem);
}

.boton-rojo-block {
  @include boton(red, block);
  width: 100%;
}
.boton-gris-no-block {
  @include boton($grisOscuro, inline-block);
}

.boton-verde-block {
  @include boton($verde, block);
}
.boton-verde-block-no-margin {
  @include boton($verde, block);
  margin-top: 0;
}
