.testimonio {
  background-color: $verde;
  font-size: 2.4rem;
  padding: 2rem;
  color: $blanco;
  border-radius: 2rem;

  blockquote {
    padding: 0 5rem;
    position: relative; //Marca que un elemento dentro del blockquote tendra la posicion de este elemento como referencia a la hora de mover la posicion
    font-weight: 100;
    &::before {
      content: "";
      background-image: url(../img/comilla.svg);
      background-repeat: no-repeat;
      width: 6rem;
      height: 8rem;
      display: block;
      position: absolute; //Marcamos que vamos a mover este objeto, entonces tenemos 4 propiedades: left,right,bottom,top
      left: -2rem;
    }
    &::after {
      content: "";
      background-image: url(../img/comilla.svg);
      background-repeat: no-repeat;
      transform: scaleX(-1);
      width: 6rem;
      height: 8rem;
      display: block;
      position: absolute; //Marcamos que vamos a mover este objeto, entonces tenemos 4 propiedades: left,right,bottom,top
      left: 23rem;
      bottom: -4rem;
    }
  }
  p {
    padding-top: 1rem;
    margin-bottom: 0rem;
    text-align: right;
    color: $blanco;
    font-weight: 100;
  }
}
