.contenido-nosotros {
  @include tablet {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 2rem;
  }
}

.texto-nosotros {
  text-align: center;
  blockquote {
    font-size: 2.5rem;
    font-weight: bold;
    @include tablet {
      margin-top: 0;
    }
  }
  @include tablet {
    text-align: left;
  }
}
