/** Media Queries **/
@mixin telefono {
  @media (min-width: #{$telefono}) {
    @content;
  }
}

@mixin tablet {
  @media (min-width: #{$tablet}) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: #{$desktop}) {
    @content;
  }
}
@mixin grid($columnas, $espaciado) {
  display: grid;
  gap: $espaciado;
  grid-template-columns: repeat($columnas, 1fr);
}

@mixin boton($bgColor, $display, $padd1: 1rem, $padd2: 3rem, $size: 1.4rem) {
  background-color: $bgColor;
  color: $blanco;
  font-weight: $bold;
  text-decoration: none;
  padding: $padd1 $padd2;
  text-align: center;
  margin-top: 2.5rem;
  display: $display;
  font-size: $size;
  border: none;
  cursor: pointer;
  &:hover {
    background-color: darken($bgColor, 5);
  }
}
