.formulario {
  p {
    font-size: 1.4rem;
    color: $grisOscuro;
    margin: 0;
  }
  legend {
    font-size: 2rem;
    color: $grisOscuro;
  }
  label {
    font-weight: bold;
    text-transform: uppercase;
    display: block;
  }
  textarea,
  select,
  input:not([type="submit"]) {
    //Selector mediante atributos
    padding: 1rem;
    display: block;
    width: 100%; //Ocupa todo el espacio disponible de izquierda a derecha
    background-color: $blanco;
    border: 1px solid $gris;
    border-radius: 1rem;
    margin-bottom: 2rem;
  }
  textarea {
    height: 20rem;
  }
}

.formulario-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  input[type="radio"] {
    margin: 0;
    width: auto;
  }
}
