.entrada-blog {
  margin-bottom: 2rem;
  border-bottom: 0.3rem solid $gris;
  &:last-of-type {
    border-bottom: none;
    margin-bottom: 0;
  }
  @include tablet {
    margin-bottom: 0rem;
  }
}

@include tablet {
  .contenedor-testimonios-blog {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
    align-items: center;
  }
}
.informacion-meta {
  span {
    color: $amarillo;
  }
}

.contenido-blog {
  a {
    color: $negro;
    display: block;
    margin-top: 2rem;
  }

  h4 {
    margin: 0;
    line-height: 1.4;
    &::after {
      content: "";
      display: block;
      width: 15rem;
      height: 0.5rem;
      background-color: $verde;
      margin-top: 1rem;
    }
  }
}
