  text-decoration: none;
  padding: 2rem 2rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  font-size: 2.5rem;
  border: none;
  cursor: pointer;
}
.boton-contacta:hover {
  background-color: #c77808;
}

.boton-rojo-block {
  background-color: red;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  font-size: 1.4rem;
  border: none;
  cursor: pointer;
  width: 100%;
}
.boton-rojo-block:hover {
  background-color: #e60000;
}

.boton-gris-no-block {
  background-color: #333333;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  font-size: 1.4rem;
  border: none;
  cursor: pointer;
}
.boton-gris-no-block:hover {
  background-color: #262626;
}

.boton-verde-block {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  font-size: 1.4rem;
  border: none;
  cursor: pointer;
}
.boton-verde-block:hover {
  background-color: #619800;
}

.boton-verde-block-no-margin {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  font-size: 1.4rem;
  border: none;
  cursor: pointer;
  margin-top: 0;
}
.boton-verde-block-no-margin:hover {
  background-color: #619800;
}

.modo-noche {
  background-color: #1a1a1a;
}
.modo-noche p {
  color: #FFFFFF;
}
.modo-noche h1,
.modo-noche h2,
.modo-noche h3,
.modo-noche h4 {
  color: #e1e1e1;
}
.modo-noche .anuncio {
  background-color: #333333;
  border: 1px solid #333333;
}
.modo-noche .anuncio .precio {
  color: #71B100;
}
.modo-noche blockquote {
  color: #e1e1e1;
}
.modo-noche li img {
  filter: invert(100%);
}
.modo-noche .formulario label,
.modo-noche .formulario legend,
.modo-noche .formulario p {
  color: #e1e1e1;
}
.modo-noche .formulario textarea,
.modo-noche .formulario select,
.modo-noche .formulario input:not([type=submit]) {
  background-color: #333333;
  color: #FFFFFF;
  border: 1px solid #333333;
}
.modo-noche table.propiedades {
  color: #FFFFFF;
}
.modo-noche table.propiedades tbody tr {
  border-bottom: 0.5rem solid #333333;
}

.no-webp .header-inicio {
  background-image: url(../img/header.jpg);
}

.webp .header-inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0rem 3rem 0rem;
}
.header.header-inicio {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .header.header-inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.header-inicio .contenido-header {
    min-height: 70rem;
  }
}
.header .contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.header h1 {
  color: #FFFFFF;
  text-align: left;
  max-width: 60rem;
  font-weight: bold;
}

.footer {
  padding: 2.5rem;
  background-color: #333333;
  margin-bottom: 0;
}
.footer .contenido-footer {
  padding-top: 2.5rem;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.barra {
  display: flex;
  flex-direction: column;
  padding-top: 3rem;
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navegacion {
  height: 0;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.5s ease-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    height: 100%;
    visibility: visible;
    opacity: 1;
  }
}
.navegacion a {
  font-size: 1.8rem;
  color: #FFFFFF;
  text-decoration: none;
  display: block;
  text-align: center;
}
.navegacion a:hover {
  color: #E08709;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

.contenedor-navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .contenedor-navegacion {
    align-items: flex-end;
  }
}
.contenedor-navegacion .dark-mode {
  margin: 2rem 0;
  width: 3rem;
  filter: invert(100%);
}
.contenedor-navegacion .dark-mode:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .contenedor-navegacion .dark-mode {
    margin-top: 0;
  }
}

.mobile-menu {
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: 100%;
  transition: visibility 0.5s ease-out, opacity 0.5s linear, height 0.5s linear;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 2rem;
    gap: 2rem;
  }
}

.icono {
  padding-top: 5rem;
  text-align: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .icono {
    padding-top: 0rem;
  }
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 2rem;
    margin-top: 0;
  }
}
.anuncio {
  background-color: #eeeeee;
  border: 1px solid #c8c8c8;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .anuncio {
    margin-top: 0;
  }
}
.anuncio .contenido-anuncio {
  padding: 2rem;
}
.anuncio h3 {
  font-weight: bold;
}
.anuncio h3,
.anuncio p {
  margin: 0;
  text-align: center;
}

.precio {
  color: #71B100;
  font-weight: bold;
  font-size: 3rem;
  text-align: center;
  padding-top: 1rem;
}

.iconos-caracteristicas {
  display: flex;
  padding: 0;
  justify-content: space-evenly;
}

li {
  display: flex;
}
li img {
  margin-right: 2rem;
  min-width: 2.5rem;
}
li p {
  font-size: 2.5rem;
  padding-right: 1.5rem;
}
li:last-child p {
  padding-right: 0rem;
}

.webp .contacto-banner {
  background-image: url(../img/encuentra.webp);
}

.no-webp .contacto-banner {
  background-image: url(../img/encuentra.jpg);
}

.contacto-banner {
  background-size: cover;
  background-position: center center;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 8rem 0rem;
}
.contacto-banner h2 {
  font-weight: bold;
}
.contacto-banner h2,
.contacto-banner p {
  color: #FFFFFF;
}

.entrada-blog {
  margin-bottom: 2rem;
  border-bottom: 0.3rem solid #e1e1e1;
}
.entrada-blog:last-of-type {
  border-bottom: none;
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .entrada-blog {
    margin-bottom: 0rem;
  }
}

@media (min-width: 768px) {
  .contenedor-testimonios-blog {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
    align-items: center;
  }
}
.informacion-meta span {
  color: #E08709;
}

.contenido-blog a {
  color: #000000;
  display: block;
  margin-top: 2rem;
}
.contenido-blog h4 {
  margin: 0;
  line-height: 1.4;
}
.contenido-blog h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}

.testimonio {
  background-color: #71B100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testimonio blockquote {
  padding: 0 5rem;
  position: relative;
  font-weight: 100;
}
.testimonio blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonio blockquote::after {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  transform: scaleX(-1);
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: 23rem;
  bottom: -4rem;
}
.testimonio p {
  padding-top: 1rem;
  margin-bottom: 0rem;
  text-align: right;
  color: #FFFFFF;
  font-weight: 100;
}

.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: bold;
  text-transform: uppercase;
  display: block;
}
.formulario textarea,
.formulario select,
.formulario input:not([type=submit]) {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}

.formulario-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario-contacto input[type=radio] {
  margin: 0;
  width: auto;
}

@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 2rem;
  }
}

.texto-nosotros {
  text-align: center;
}
.texto-nosotros blockquote {
  font-size: 2.5rem;
  font-weight: bold;
}
@media (min-width: 768px) {
  .texto-nosotros blockquote {
    margin-top: 0;
  }
}
@media (min-width: 768px) {
  .texto-nosotros {
    text-align: left;
  }
}

.alerta {
  margin: 1rem 0;
  text-align: center;
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: bold;
}
.alerta.error {
  background-color: red;
}
.alerta.exito {
  background-color: #71B100;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0rem;
  border-collapse: collapse;
}
table.propiedades th {
  color: #FFFFFF;
  padding: 2rem;
}
table.propiedades .imagen-preview {
  display: block;
  margin: 0 auto;
  width: 20rem;
}
table.propiedades tbody tr {
  border-bottom: 0.5rem solid #e1e1e1;
}
table.propiedades tbody tr:last-child {
  border-bottom: none;
}
table.propiedades tbody tr td {
  text-align: center;
  padding: 2rem 0rem;
}

.imagen-tabla {
  width: 30rem;
}

.cabecera-verde {
  background-color: #71B100;
}

.cabecera-amarilla {
  background-color: #E08709;
}

.cabecera-gris {
  background-color: #333333;
}